:root {
    --card-spacing: 1.5vh;
}

.card-deck {
    margin: 5vh auto;
    display: grid;
    grid-gap: var(--card-spacing);
    grid-template-columns: repeat(6, 1fr);
    grid-auto-rows: 1fr;
}

.card {
    border-radius: 5px;
    transition: transform 250ms;
    position: relative;
    width: 100%;
    height: 0;
    padding-bottom: calc(100%);
    background-size: 100%, 100%;
}

.card:hover {
    z-index: 1;
    transform: scale(1.07);
}

.card-transparency {
    width: 100%;
    height: 0;
    padding-bottom: 100%;
    opacity: 0;
    border-radius: 5px;
}

.card-header {
    position: absolute;
    margin: 0;
    width: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
}

.card:hover .card-transparency {
    transition: opacity 100ms;
    opacity: 1;
}